.links{
  color: blue;
}
.cursor{
  
  position: fixed;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  transition: 0.1s;
  transform: translate(-50%,-50%);
  pointer-events: none;
  mix-blend-mode: difference;
  visibility: hidden;
}
.header-ul .links:hover ~ .cursor{
  visibility: visible;
  transform: scale(6);
}

